<template>
  <div class="app-list" id="lists">
     <ul  v-infinite-scroll="loadMore"
          infinite-scroll-disabled="loading"
          infinite-scroll-distance="10">
        <li v-for="item in list" class="list">
          <div class="list-item list-l">
            <img class="list-img" v-lazy.list=item.img>
          </div>
          <div class="list-item list-c">
            <div class="intro-title">{{ item.name }}</div>
            <div class="intro-count">{{ item.count }}次下载<span class="blank-20"></span>{{ item.size }}kb</div>
            <div class="intro-content">{{ item.intro }}</div>
          </div>
          <a :href="item.down"><div class="list-item list-r"><mt-button plain size="small">
            <p class="down-text">下载</p></mt-button></div></a>
        </li>
      </ul>
  </div>
</template>

<script>
  let a = []
  let b = []
  let page = 1

  import axios from 'axios'

  axios({
    method: 'post',
    url: '/api/soft/list',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    },
    data: {
      page: page
    }
  }).then(function (response) {
      page += 1
    a = response.data.data
  }).catch(function (error) {
    console.log(error)
  })

  export default {
    name: 'soft',
    data () {
      return {
        msg: '',
        list: a,
        tPage: page,
        allLoaded: false,
        bottomStatus: ''
      }
    },
    methods: {
      handleBottomChange (status) {
        this.bottomStatus = status
      },
      loadMore () {
        setTimeout(() => {
          let _this = this
          _this.loading = true
          console.log(_this.tPage)
          axios({
            method: 'post',
            url: '/api/soft/list',
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            data: {
              page: _this.tPage
            }
          }).then(function (response) {
            console.log(response)
            b = response.data.data
            _this.tPage += 1
            // console.log(b)
            if (_this.list.length > 0) {
              a = [...a, ...b]
            } else {
              a = b
            }
            _this.loading = true
            setTimeout(() => {
              _this.list = a
              _this.loading = false
            }, 20)
          }).catch(function (error) {
            console.log(error)
          })
        }, 2500)
      }

    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" rel="stylesheet/stylus" scoped>

</style>



